$iconColor: #f18822;
$backgroundClolor: #f3f5f6;

.features-banner {
  background-color: $backgroundClolor;
  margin: -6px;
  padding: 36px 0 30px 0;
  font-family: "Roboto Mono", monospace;
  box-sizing: border-box;

  @media (max-width: 500px) {
    margin: 0;
  }

  .fas {
    color: $iconColor;
    font-size: 4rem;

    @media (max-width: 500px) {
      font-size: 2rem;
      // margin-bottom: 3rem;
    }
  }

  ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: start;
    flex-wrap: wrap;
    margin: 0;

    @media (max-width: 500px) {
      padding: 0;
    }
  }

  ul li {
    display: flex;
    align-items: center;

    @media (max-width: 500px) {
      margin-bottom: 2rem;
    }
  }

  ul li span {
    text-align: left;
    text-align-last: left;
    margin-left: 1rem;
    width: 6rem;
  }
}
